import Swiper from 'swiper';
import {A11y, Navigation, Pagination, FreeMode, Thumbs, Autoplay} from 'swiper/modules';
window.Swiper = Swiper;



export function initSwiper() {
    const startSlider = new Swiper('.start-slider', {
        modules: [Navigation, A11y, Autoplay],
        slidesPerView: 1,
        spaceBetween: 10,
        loop: true,
        autoplay: {
            delay: 3500,
            disableOnInteraction: false,
        },

        navigation: {
            nextEl: '.swiper-arrow_next',
            prevEl: '.swiper-arrow_prev'
        },
    });
}

